import { Mutation } from "react-apollo";
import gql from "graphql-tag";
import React from "react";
import Router from "next/router";
import Joi from "joi-browser";
import {allProjectsQuery, allProjectsQueryVars} from "./ProjectList";



const CREATE_PROJECT = gql`
  mutation project_operations(
    $id: String
    $name: String
    $description: String
  ) {
    project_operations(name: $name, description: $description) {
      name
      description
    }
  }
`;

const schema = {
  name: Joi.string()
    .required()
    .error(errors => {
      return {
        message: "Name is required!"
      };
    }),
    description: Joi.string()
    .required()
    .error(errors => {
      return {
        message: "Description is required!"
      };
    })
};

class Projectform extends React.Component {
  
  state = {
    id: "",
    name: "",
    description: "",
    errors: ""
};
  constructor(props) {
        super(props);
        if (props.project) {
            this.state = {
            id: props.project.id,
            name: props.project.name,
            description: props.project.description
        };
    }  
    console.log(this.state);
  }

  handleChange = event => {
   
    const { name, value } = event.target;
    this.setState({ [name]: value });
  };

  validate(name, description) {
    let errors = "";
    const result = Joi.validate(
      {
        name: name,
        description: description
      },
      schema
    );
    if (result.error) {
      errors = result.error.details[0].message;
    }
    return errors;
  }

  render() {
    let input;
    const { errors } = this.state;
   
    return (

      <Mutation mutation={CREATE_PROJECT}
      update={(cache, { data: { project_operations } }) => {
        const { projectlist } = cache.readQuery({
          query: allProjectsQuery,
          variables: allProjectsQueryVars
        });
        cache.writeQuery({
          query: allProjectsQuery,
          variables: allProjectsQueryVars,
          data: { projectlist: projectlist.concat([project_operations]) }
        });
      }}
    >
         {(project_operations, { loading, error }) => (
                <div>
                  <form
                    onSubmit={e => {
                      e.preventDefault();
                      const errors = this.validate(
                        e.target.name.value,
                        e.target.description.value
                      );
                      if (errors) {
                        this.setState({ errors });
                        return;
       
                     }
                    if (!errors) {
                     project_operations({
                          variables: {
                            id:  e.target.id.value,
                            name: e.target.name.value,
                            description: e.target.description.value,
                          }
                        }).then(() => Router.push("/"));
                      }
                    }}
                  >
                    {errors && <p>{errors}</p>}
                    <input
                      type="text"
                      name="name"
                      id="name"
                      placeholder="Project Name"
                      value={this.state.name}
                      onChange={this.handleChange}
                    />
                    <input
                      name="description"
                      id="description"
                      value={this.state.description}
                      placeholder="Description"
                      onChange={this.handleChange}
                    />
                     <input type="hidden" name="id" id="id" value={this.state.id} />
                    <button type="submit">Add Account</button>
                    <style jsx>{`
                      form {
                        border-bottom: 1px solid #ececec;
                        padding-bottom: 20px;
                        margin-bottom: 20px;
                      }
                      h1 {
                        font-size: 20px;
                      }
                      input {
                        display: block;
                        margin-bottom: 10px;
                      }
                      form button {
                        margin-right: 10px;
                      }
                    `}</style>
                  </form>
                  {loading && <p>Loading...</p>}
                  {error && <p>Error :( Please try again</p>}
                </div>
              )}
            </Mutation>
          );
  }
}

export default Projectform;